<template>
  <section class="participant">
    <img
      v-if="name"
      :src="require(`../assets/images/${name}.png`)"
    >
    <TemplateRenderer
      v-if="bio"
      class="content"
      :node="templateRootNode"
      :slots="$slots"
    />
  </section>
</template>

<script>
const renderNodeContent = (createElement, node, slots) => (!node.childNodes.length
  ? node.textContent
  : Array.from(node.childNodes)
    .filter((n) => [Node.ELEMENT_NODE, Node.TEXT_NODE].includes(n.nodeType))
    .map((n) => {
      switch (n.tagName) {
        case 'b':
          return createElement('b', renderNodeContent(createElement, n, slots));
        case 'br':
          return createElement('br');
        case undefined:
          return n.textContent;
        default:
          return slots[n.tagName];
      }
    }));

const TemplateRenderer = {
  functional: true,
  props: {
    node: { type: Node, required: true },
    slots: { type: Object, required: true },
  },
  render(createElement, { data, props }) {
    return createElement(
      'div',
      { class: data.staticClass },
      renderNodeContent(createElement, props.node, props.slots),
    );
  },
};

export default {
  components: { TemplateRenderer },
  props: ['name', 'bio'].reduce((p, k) => ({ ...p, [k]: { type: String, required: true } }), {}),
  computed: {
    templateRootNode() {
      return new DOMParser()
        .parseFromString(`<root>${this.bio}</root>`, 'text/xml').childNodes[0];
    },
  },
};
</script>
